import styled from 'styled-components';

const Content = styled.div`
  border-bottom: 1px solid #f2f2f2;
  padding: 15px 30px;

  &:last-of-type {
    border-bottom: 0;
    border-top: 1px solid #f2f2f2;
    margin-top: auto
  }
`;

export default Content;



