import styled from 'styled-components';

const Button = styled.a`
  background-color: #104986;
  border-radius: 3px;
  color: white;
  display: block;
  font-size: 14px;
  font-weight: 400;
  margin: 15px 0;
  padding: 15px 30px;
  text-decoration: none;
  text-align: center;
`;

export default Button;



